<script>
    export let progress;
    let determinate = false;
    if (typeof progress != Number) {
        determinate = false;
    } else {
        determinate = true;
    }
</script>

{#if determinate}
    <div class="progress">
        <div class="determinate" style={`width: ${progress}%`} />
    </div>
{:else}
    <div class="progress">
        <div class="indeterminate" />
    </div>
{/if}

